#arrow{
	display: block; 
	z-index: 1000; 
	position: fixed;
	margin: 15px 10px 10px 27px;
}
#header_earth{
    padding-top: 15px;
    text-align:center;
    font-weight: 500;
    line-height: 29px;
    color:white;
}
#header_tasks{
    padding-top: 20px;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    color: #39267e;
}
.progress {
    display: block;
    padding: 15px;
    margin: 10px auto;
    border-radius: 15px;
    font-size: 14px;
    color: #fff;
    width: 50%;
    min-width: 200px;
    background: -webkit-linear-gradient(0.8deg,#6fd2d3 .22%,#3be797 99.78%);
    background: linear-gradient(89.2deg,#6fd2d3 .22%,#3be797 99.78%);
    box-shadow: 0 0 10px rgba(0,0,0,.15);
}
.bold{
    font-weight: 700;
}
.line {
    border: 1px solid hsla(0,0%,100%,.6);
    border-radius: 5px;
    display: inline;
    margin: 0 6px 0 3px;
}
#content{
    margin: 0;
  user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  font-family: 'Montserrat', sans-serif;
}
#div_earth{
    display:flex;
    margin: 0 auto;
    border-radius: 50%;
    align-items:center;
    justify-content: center;
}
#img_earth0{
    background: url(../../static/media/bg.be0f6922.png);
    max-width: 90vw;
}
#img_earth1{
    max-width: 90vw;
    position:absolute; 
    opacity:1.0;
}
.fact {
    color: #ff9533;
}
.fact2{
    
}
#div_fix {
    border-radius: 15px;
    margin-left: 10%;
    margin-right: 10%;
    width: 80%;
    background: -webkit-linear-gradient(342.48deg,#6ed2d2 1.19%,#3ce699 98.21%);
    background: linear-gradient(107.52deg,#6ed2d2 1.19%,#3ce699 98.21%);
    color: #fff;
    padding: 25px 0;
}
#header_fix {
    text-align: center;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 29px;
}
#content_fix {
    margin: auto 10%;
}
#content{
    font-family: Monserrat,sans-serif;
}
#img {
    width: 100%;
    height: 220px;
    box-shadow: 0 4px 10px rgba(0,0,0,.1);
    border-radius: 0 0 20px 20px;
}
#header_main {
    text-align: center;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: #000;
}
#text {
    font-family: Montserrat,sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 22px;
    color: #000;
    margin: 20px;
}
